.product-card {
  width: 18rem;
  cursor: pointer;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.product-card-img {
  height: 200px;
  object-fit: cover;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.product-card-body {
  min-height: 180px;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .product-card {
    width: 100%;
    margin: 0 0 15px 0;
  }
  .product-card-img {
    height: 150px;
  }
  .product-card-body {
    min-height: auto;
  }
}
