.contactus-container {
  margin-top: 5rem;
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  padding: 1rem;
  justify-content: center;
  align-items: flex-start;
}

.contactus-image {
  margin-top: none;
  display: flex;
  flex: 1 1 300px;
  max-width: 1000px;
}
.contactus-content {
  flex: 1 1 300px;
  max-width: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.contactus-info {
  padding: auto;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.contactus-info p {
  flex: 1 1 150px;
  margin-top: 0.5rem;
  display: flex;
  align-items: center;
  font-weight: 500;
  font-size: 1rem;
}
