.banner-img {
  margin-top: 50px;
}

.home-page h1 {
  color: #343a40; /* Dark gray */
  font-family: "Playfair Display", serif;
}

.home-page {
  display: flex;
  flex-wrap: wrap;
  background-color: #ffffff; /* White background */
  color: #212529; /* Dark text */
}

.home-page .card {
  background-color: rgba(248, 249, 250, 0.8); /* Light gray transparent */
  width: 18rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #212529;
}
.home-page .card:hover {
  transform: translateY(-8px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
}

.home-page .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.home-page .card-price {
  color: #198754; /* Bootstrap green */
  font-weight: bold;
}
.home-page button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 2px;
  font-size: 14px;
  font-weight: bold;
  transition: background-color 0.3s ease;
  background-color: #0d6efd; /* Blue */
  color: white;
  border: none;
}

.home-page button:hover {
  background-color: #0a58ca;
  color: white;
}

.home-page .card-text {
  color: #212529;
  min-height: 48px;
}

.home-page .card img:hover {
  transform: scale(0.95);
  transition: transform 0.3s ease;
}

/* Filters section improvements */
.filters h4 {
  margin-top: 60px;
  color: #343a40 !important;
  border-bottom: 2px solid #dee2e6;
  padding-bottom: 8px;
  font-weight: 600;
}

.filters {
  padding: 0 15px;
}

.filters .form-select,
.filters .form-control {
  margin-bottom: 15px;
  border-radius: 5px;
  padding: 8px 12px;
  font-size: 14px;
  background-color: #ffffff;
  color: #212529;
  border: 1px solid #ced4da;
}

.filters button {
  background-color: #0d6efd; /* Blue */
  width: 100%;
  border: none;
  border-radius: 0;
  margin-top: 20px;
  color: white;
}

.loadmore {
  color: #0d6efd; /* Blue */
  font-weight: bold;
  font-size: 20px !important;
}

/* Smaller add to cart button on home page product cards */
.home-page .product-card .btn-primary {
  padding: 4px 8px;
  font-size: 12px;
  width: auto;
  border-radius: 4px;
  background-color: #0d6efd; /* Blue */
  border: none;
  color: white;
}

.home-page .product-card .btn-primary:hover {
  background-color: #0a58ca;
  color: white;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .home-page {
    flex-direction: column;
  }
  .filters {
    width: 100% !important;
    margin-bottom: 20px;
  }
  .col-md-9 {
    width: 100% !important;
  }
  .home-page .card {
    width: 100% !important;
    margin: 10px 0 !important;
  }
  .home-page .card-img-top {
    height: 3000px !important;
  }
}
