/* Combined styles for Layout component from Global.css and Theme.css */

.container-wide {
  max-width: 2000px;
  margin: 0 auto;
  padding: 0 20px;
  background: rgba(248, 249, 250, 0.85); /* Light translucent background */
  border-radius: 12px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.1); /* Subtle shadow */
  transition: background 0.5s ease, box-shadow 0.5s ease;
}

@media (max-width: 1600px) {
  .container-wide {
    max-width: 1400px;
  }
}

@media (max-width: 1200px) {
  .container-wide {
    max-width: 1000px;
  }
}

@media (max-width: 768px) {
  .container-wide {
    max-width: 100%;
    padding: 0 10px;
  }
}
