/* Combined styles for Header component from Global.css and Theme.css */

/* Navbar styles */
.navbar {
  background: linear-gradient(90deg, #f8f9fa, #e9ecef) !important;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  transition: background 0.5s ease, box-shadow 0.5s ease;
}

.navbar .navbar-brand,
.navbar .nav-link {
  color: #343a40 !important; /* Dark gray */
  font-weight: 700;
  letter-spacing: 0.05em;
  transition: color 0.3s ease, transform 0.3s ease;
}

.navbar .nav-link:hover,
.navbar .nav-link:focus {
  color: #495057 !important; /* Medium gray */
  transform: scale(1.1);
}

.navbar-toggler {
  border-color: rgba(52, 58, 64, 0.3);
  transition: border-color 0.3s ease;
}

.navbar-toggler:hover {
  border-color: #495057;
}

.navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba%2834, 34, 34, 0.7%29' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
  transition: background-image 0.3s ease;
}

/* Links */
a {
  color: #343a40;
  text-decoration: none;
  transition: color 0.3s ease, transform 0.3s ease;
}

a:hover,
a:focus {
  color: #495057;
  text-decoration: underline;
  transform: scale(1.05);
}

/* Buttons */
button,
.btn {
  border-radius: 8px !important;
  background-color: #343a40;
  color: white !important;
  font-weight: 600;
  padding: 10px 20px;
  border: none;
  box-shadow: 0 4px 15px rgba(52, 58, 64, 0.4);
  transition: background-color 0.3s ease, color 0.3s ease, box-shadow 0.3s ease, transform 0.2s ease;
  cursor: pointer;
}

button:hover,
.btn:hover {
  background-color: #495057 !important;
  color: white !important;
  box-shadow: 0 6px 20px rgba(73, 80, 87, 0.6);
  transform: translateY(-2px);
}

/* Responsive utilities for navbar and buttons */
@media (max-width: 1200px) {
  .navbar .navbar-brand,
  .navbar .nav-link {
    font-size: 16px;
  }
}

@media (max-width: 992px) {
  .navbar .navbar-brand,
  .navbar .nav-link {
    font-size: 14px;
  }
}

@media (max-width: 768px) {
  .navbar .navbar-brand,
  .navbar .nav-link {
    font-size: 14px;
  }
  button,
  .btn {
    font-size: 16px;
    padding: 10px 15px;
  }
}

@media (max-width: 576px) {
  .navbar .navbar-brand,
  .navbar .nav-link {
    font-size: 12px;
  }
  button,
  .btn {
    font-size: 14px;
    padding: 8px 12px;
  }
}
